import { useRef } from 'react';
import { useDrag, useDrop } from 'react-dnd';
import { useDispatch, useSelector } from 'react-redux';
import { setDroppedItems } from '../store/ConstructorSlice';
import { RootState } from '../store';
import { DragItem, DraggableItem } from '../types';

export const useItemDnD = <T extends HTMLDivElement>(
  item: DraggableItem,
  index: number,
  desk: 'palete' | 'constructor'
) => {
  const { constructorModeEnable, droppedItems } = useSelector((state: RootState) => state.constructorReducer);
  const ref = useRef<T>(null);
  const dispatch = useDispatch();

  const [{ isDragging }, drag] = useDrag({
    type: 'components',
    item: { component: item, idx: index },
    canDrag: constructorModeEnable && (droppedItems.includes(item) && item.id === 1 ? false : true),
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  const [{ isOver, fromTop, droppedItem }, drop] = useDrop({
    accept: 'components',
    canDrop: () => desk === 'constructor',
    drop: (item: DragItem) => {
      const { component } = item;

      if (component.id === 1) {
        dispatch(setDroppedItems([component, ...droppedItems]));
        return;
      }
      if (droppedItems[0].id === 1 && index === 0) {
        return;
      }
      const newItems = droppedItems.filter((item) => item.id !== component.id);
      newItems.splice(index, 0, component);
      dispatch(setDroppedItems(newItems));
    },
    collect: (monitor) => ({
      isOver: !!monitor.isOver() && desk === 'constructor',
      fromTop: monitor.getItem()?.idx < index,
      droppedItem: monitor.getItem()?.component,
    }),
  });

  drag(drop(ref));

  return { ref, isOver, fromTop, isDragging, droppedItem };
};

export default useItemDnD;
