import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../store';
import { removeItem } from '../../store/ConstructorSlice';
import { useItemDnD } from '../../hooks';
import { DraggableItem } from '../../types';
import './DragItemContainer.css';

type Props = {
  item: DraggableItem;
  children: any;
  index: number;
  desk: 'palete' | 'constructor';
};

const DragItemContainer = ({ item, index, desk, children }: Props) => {
  const { ref, isOver, fromTop, droppedItem, isDragging } = useItemDnD(item, index, desk);
  const dispatch = useDispatch();

  const { droppedItems, constructorModeEnable } = useSelector(
    (state: RootState) => state.constructorReducer
  );

  const handleRemoveItem = () => {
    if (!constructorModeEnable) {
      return;
    }
    dispatch(removeItem(item.id));
  };

  return (
    <div
      ref={ref}
      onDoubleClick={handleRemoveItem}
      draggable={droppedItems.find((i) => i.id === 1) && item.id === 1 ? false : true}
      className={`drag-container ${isDragging && desk === 'constructor'? ' disabled' : ''}`}
    >
      {isOver && !fromTop && droppedItem.id !== 1 && item.id !== 1 && <span className="drop-highlight_top "></span>}
      {children}
      {isOver && fromTop && droppedItem.id !== 1 && <span className="drop-highlight_bottom"></span>}
    </div>
  );
};

export default DragItemContainer;
