import React from 'react';
import './Button.css';

type Props = {
  onClick: () => void;
  children: any;
  variant?: 'contained' | 'outlined';
  disabled?: boolean;
};

export const Button = ({ onClick, variant, disabled, children }: Props) => {
  return (
    <button
      onClick={onClick}
      className={`button ${disabled? 'button_disabled' : ''} ${variant === 'contained' ? 'button-contained' : ''}`}
    >
      {children}
    </button>
  );
};

export default Button;
