import React from 'react';
import './SwitchButton.css';

type Props = {
  onClick: () => void;
  children: any;
  isActive: boolean;
  icon?: JSX.Element;
};

const SwitchButton = ({ onClick, isActive, icon, children }: Props) => {
  return (
    <div
      onClick={onClick}
      className={isActive ? 'switch-button switch-button_active' : 'switch-button'}
    >
      {icon && (
        <div className={`switch-button__icon ${isActive ? 'switch-button__icon_active' : ''}`}>
          {icon}
        </div>
      )}
      {children}
    </div>
  );
};

export default SwitchButton;
