import React from 'react';
import { useSelector } from 'react-redux';
import DragItemContainer from '../DragItemContainer/DragItemContainer';
import { useCalcItemsRender }from '../../hooks';
import { RootState } from '../../store';
import { Paper } from '../../components';
import './CalcPalete.css';


const CalcPalete = () => {
  const { renderElement } = useCalcItemsRender();
  const {constructorModeEnable, droppedItems } = useSelector((state: RootState) => state.constructorReducer);

  const dragableBlocks = [
    { id: 1, component: 'Display' },
    { id: 2, component: 'Operations' },
    { id: 3, component: 'Numpad' },
    { id: 4, component: 'EqualButton' },
  ];

  return (
    <div className="palete">
      {constructorModeEnable && (
        <>
          {dragableBlocks.map((item, idx) => (
            <div
              key={item.id}
              className={droppedItems.find((el) => el.id === item.id) ? 'disabled' : ''}
            >
              <DragItemContainer desk="palete" index={idx} item={item}>
                <Paper hide={!!droppedItems.find((el) => el.id === item.id)}>
                  {renderElement(item)}
                </Paper>
              </DragItemContainer>
            </div>
          ))}
        </>
      )}
    </div>
  );
};

export default CalcPalete;
